<h3 id="结算管理" tabindex="-1"><a class="header-anchor" href="#结算管理" aria-hidden="true">#</a> <div align='center'>结算管理</div></h3>
<h5 id="项目描述-8" tabindex="-1"><a class="header-anchor" href="#项目描述-8" aria-hidden="true">#</a> 项目描述：</h5>
<p>各种各样的业务系统每天会产生成千上万的订单，财务通过这些订单在财务系统中进行做账，工作量较大，
所以通过结算管理这套系统来处理所有的账单，并生成对应的凭证。系统定时拉取订单仓库中的订单数据，
按一定规则进行计算，生成凭证并抛送到财务系统（用友或金蝶），简化会计操作流程；</p>
